import { _ } from '@splunk/ui-utils/i18n';
/**
 * Add a property to the Item class to inform that it can set tokens based on item events
 * @param {LayoutItem} Item The item to enhance
 */
export default (Item, type = ['dynamic', 'static']) => {
    Item.canSetTokens = type;
    Item.interactionsFilterMetadata ??= [];
    Item.interactionsFilterMetadata.push(_('Set tokens'));

    return Item;
};
