import { _ } from '@splunk/ui-utils/i18n';
/**
 * Add a property to the Item class to inform that it can redirect to a custom url based on item events
 * @param {LayoutItem} Item The item to enhance
 */
export default (Item) => {
    Item.canSetCustomUrl = true;
    Item.interactionsFilterMetadata ??= [];
    Item.interactionsFilterMetadata.push(_('Link to custom URL'));

    return Item;
};
